import { Table, TableBody, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import CategoryRow from "./CategoryRow";
import { CategoryType } from "./CategoryList";
import axios from "axios";
import { cookies } from "next/headers";

interface Props {
    categories: CategoryType[]
}

const CategoryTable = ({ categories }: Props) => {


    const handleUpdateSubmit = async (name: string, status: number | string, id: number | string) => {
        const data = {
            name: name,
            status: status
        }

        const cookieStore = await cookies();
        const adminToken = cookieStore.get("admin_token")?.value;

        const response = await axios.put(
            `${process.env.NEXT_PUBLIC_BACKEND_URL}/admin/update/category/${id}`,
            data,
            {
                headers: {
                    Authorization: `Bearer ${adminToken}`,
                    "Content-Type": "application/json"
                }
            }
        );

        console.log("Category updated successfully:", response.data);

    }

    return (
        <Table>
            <TableHeader>
                <TableRow>
                    <TableHead className="w-[50px]">SL</TableHead>
                    <TableHead>Name</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead className="text-right">Action</TableHead>
                </TableRow>
            </TableHeader>
            <TableBody>
                {categories.map((category, index) => (
                    <CategoryRow key={category.id} category={category} index={index} handleUpdateSubmit={handleUpdateSubmit} />
                ))}
            </TableBody>
        </Table>
    );


};

export default CategoryTable;
